html {
  font-size: 16px;
}

body {
  font-family: var(--ff-body);
  font-weight: var(--fw-400);
  font-style: normal;
  font-size: var(--fs-400);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, .h99 {
  font-family: var(--ff-heading);
  font-weight: var(--fw-700);
  margin-bottom: 1rem;
  text-wrap: balance;
}

.font-weight-normal {
  font-weight: var(--fw-400) !important;
}

.font-weight-medium {
  font-weight: var(--fw-600) !important;
}

.font-weight-bold {
  font-weight: var(--fw-700) !important;
}

h1, .h1, .fs-999 {
  font-size: var(--fs-999);
  line-height: 1.1;
}

h2, .h2, .fs-900 {
  font-size: var(--fs-800);
}

h3, .h3, .fs-800, .has-x-large-font-size {
  font-size: var(--fs-700);
}

h4, .h4, .fs-700 {
  font-size: var(--fs-600);
}

h5, .h5, .fs-600 {
  font-size: var(--fs-500);
}

.fs-500, .has-medium-font-size {
  font-size: var(--fs-500);
}

.fs-400 {
  font-size: var(--fs-400);
}

strong {
  font-weight: var(--fw-600) !important;
}

.fw-bold {
  font-weight: var(--fw-700) !important;
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-200 {
  font-size: var(--fs-200);
}

h1 > span, .h1 > span {
  color: var(--col-blue-400);
  font-style: italic;
  text-decoration: underline var(--col-blue-400) solid 0.2em;
}

.max-ch {
  max-width: 60ch;
}
.max-ch.text-center {
  margin: 0 auto;
}

.max-ch--auto {
  max-width: 90ch;
  margin: auto;
}

.has-grey-400-background-color,
.bg-grey-400 {
  background-color: var(--col-grey-400);
}

.has-yellow-400-color {
  color: var(--col-yellow-400);
}

.has-blue-400-color {
  color: var(--col-blue-400);
}

.text-white {
  color: var(--col-white);
}

.bg-white {
  background: var(--col-white);
}

.button {
  display: inline-block;
  border-radius: 0;
  border: none;
  padding: 0.5rem 2rem;
  text-decoration: none;
  transition: color var(--transition);
  background-color: var(--col-blue-400);
  font-weight: var(--fw-600);
  color: var(--col-white);
}
@media (min-width: 576px) {
  .button {
    padding: 0.75rem 3rem;
  }
}
.button:hover {
  color: var(--col-yellow-400);
}

.button-outline {
  background-color: transparent;
  border: 2px solid var(--col-black);
  color: var(--col-black);
}

.button-sm {
  padding: 0.5rem 2rem;
}

header:not(main header) {
  background-color: var(--col-yellow-400);
  position: fixed;
  width: 100%;
  z-index: 9999;
  transition: transform var(--transition);
}
header:not(main header).hidden {
  transform: translateY(-100%);
}
header:not(main header) .nav-link {
  font-weight: var(--fw-600);
  font-size: var(--fs-400);
  color: var(--col-black);
}
header:not(main header) .dropdown-toggle {
  display: flex;
  align-items: center;
}
header:not(main header) .dropdown-toggle::after {
  content: "";
  border: none;
  background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 14.67 7.33'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %230073ca; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='NAV_BAR' data-name='NAV BAR'%3E%3Cpolygon class='cls-1' points='9.49 0 7.33 2.16 5.18 0 0 0 7.33 7.33 14.67 0 9.49 0'/%3E%3C/g%3E%3C/svg%3E");
  width: 15px;
  height: 7px;
  background-size: contain;
}
header:not(main header) .navbar-toggler {
  box-shadow: none;
  border: none;
  color: var(--col-black);
}
header:not(main header) #main-menu {
  margin-top: 1rem;
}
@media (min-width: 992px) {
  header:not(main header) #main-menu {
    margin-top: 0;
  }
}
header:not(main header) .header_logo {
  order: 0;
}
header:not(main header) .header_ctas {
  order: 2;
  text-align: center;
}
header:not(main header) .header_ctas .button {
  margin-bottom: 0.25rem;
}
header:not(main header) .header_ctas div > a {
  text-decoration: none;
  color: var(--col-blue-400);
  font-weight: var(--fw-600);
}
header:not(main header) .header_toggle {
  order: 3;
}
header:not(main header) .header_nav {
  order: 4;
}
@media (min-width: 992px) {
  header:not(main header) .container-xl > * {
    order: 0;
  }
  header:not(main header) .header_ctas {
    order: 1;
  }
}
header:not(main header) .dropdown-menu {
  background-color: var(--col-blue-400);
  border-radius: 0;
  border: none;
}
header:not(main header) .dropdown-menu a {
  color: var(--col-white);
}
header:not(main header) .dropdown-menu .dropdown-item {
  transition: all 100ms ease;
}
header:not(main header) .dropdown-menu .dropdown-item:hover,
header:not(main header) .dropdown-menu .dropdown-item:focus {
  background-color: var(--col-yellow-400);
  color: var(--col-black);
}

footer {
  background-color: var(--col-black);
  color: var(--col-white);
}
footer .footer__logo {
  max-width: 187px;
}
@media (min-width: 768px) {
  footer .footer__border {
    border-left: 1px solid var(--col-yellow-400);
  }
}
footer .menu-title {
  color: var(--col-yellow-400);
  margin-bottom: 1em;
}
footer .menu {
  list-style: none;
  padding-inline-start: 0;
  font-size: var(--fs-300);
}
footer a {
  color: var(--col-white);
  text-decoration: none;
}
footer a:hover {
  color: var(--col-yellow-400);
}
footer .social_icons {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
footer .social_icons a {
  display: grid;
  place-content: center;
  width: 32px;
  height: 32px;
  border-radius: 100vw;
  background-color: var(--col-white);
  color: var(--col-black);
  transition: background-color var(--transition);
}
footer .social_icons a:hover {
  background-color: var(--col-yellow-400);
}
footer .colophon {
  font-size: var(--fs-200);
}

