@media (min-width: 600px) {
  .process_steps {
    /* Ensure numbers are centered */
    /* Odd items */
    /* Even items */
  }
  .process_steps ul li {
    display: grid;
    grid-template-columns: 1fr auto 1fr; /* Three columns: left, center, right */
    gap: 1.5rem;
    align-items: center;
    position: relative;
  }
  .process_steps ul li::before {
    content: "";
    position: absolute;
    left: 50%; /* Center the line horizontally */
    transform: translateX(-50%);
    top: 0;
    bottom: 0;
    width: 4px;
    background-color: var(--col-blue-400);
    z-index: -1; /* Make sure the line is behind the numbers */
  }
  .process_steps ul li:first-child::before {
    top: 1.5rem; /* Start line from the middle of the first circle */
  }
  .process_steps ul li:last-child::before {
    bottom: 1.5rem; /* End line at the middle of the last circle */
  }
  .process_steps .number {
    grid-column: 2/3; /* Center column */
    text-align: center;
    padding-block: 1.5rem;
  }
  .process_steps .item {
    text-align: center;
